.urlInput {
    max-width: 800px;
    width: 80%;
    margin: auto;
    padding: 10px;
    margin-top: 20px;
}

h1 {
  display: flex;
  text-align: center;
  justify-content: center;
}

#inputUrl {
  resize: none;
}

.parameterEditor {
    max-width: 800px;
    width: 80%;
    margin: auto;
    padding: 10px;
    margin-top: 20px;
}

#parameterEditorCancelButton {
  float: left;
  width: 45%;
}

#parameterEditorSubmitButton {
  float: right;
  width: 45%;
}

.parameterEditorKeyColumn {
  resize: none;
  width: 100%;
  border: none;
}

#convertedUrl {
  background-color: #d3d3d3;
  width: 100%;
  overflow-wrap: break-word;
  padding: 10px;
  margin-bottom: 10px;
}

#parameterEditorButtonsContainer {
  margin-bottom: 20px;
  overflow: auto;
}
